import React, { Component } from 'react';
import { H1, H2, H5, Span } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Img, TwoCol, Br } from '../css/container.js';
import { meContent } from '../content.js';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';

class AboutMe extends Component {
  handleClick = e => {
    const target = document.getElementById(e.target.title);
    target.scrollIntoView();
  }
  render() {
    return (
      <div>
        <Navbar/>
        <OuterContainer>
          <InnerContainer>
            <br/><br/><br/>
            <TwoCol even>
              <div>
              <br/><Br/><Br/><Br/><Br/>
              <H1>{meContent.title}<Span subtitle></Span></H1>
              <H5 dangerouslySetInnerHTML={{ __html: meContent.intro }} />
              </div>
              <Img border src={meContent.img} alt={meContent.img} theme={{width:"100%"}}/>
            </TwoCol>
            <br/><br/>
            <InnerContainer>
              {/**<H2>I love ...</H2>
              <div>
                <H5>Neuroscience</H5>
                <H5>Cooking</H5>
              </div>
              <H2>I like ...</H2>
              <H5>Neuroscience</H5>**/}
              <div>
                <H2>My Passion about UX</H2>
                <H5 dangerouslySetInnerHTML={{ __html: meContent.passion }} />
              </div>
              <div>
                <br/><br/>
                <H2>MY SKILLS</H2>
                <TwoCol even>
                <H5 dangerouslySetInnerHTML={{ __html: meContent.skill1 }} />
                <H5 dangerouslySetInnerHTML={{ __html: meContent.skill2 }} />
                </TwoCol>
              </div>
            </InnerContainer>
          </InnerContainer>
        </OuterContainer>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default AboutMe;
